@font-face {
  font-style: normal;
  font-weight: 500;
  src: local("Roboto Medium"), local("Roboto-Medium"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-family: "Roboto";
}
@font-face {
  font-style: normal;
  font-weight: 900;
  src: local("Roboto Black"), local("Roboto-Black"), url("../fonts/Roboto-Black.woff") format("woff");
  font-family: "Roboto";
}
@font-face {
  font-style: normal;
  font-weight: 300;
  src: local("Roboto Light"), local("Roboto-Light"), url("../fonts/Roboto-Light.woff") format("woff");
  font-family: "Roboto";
}
@font-face {
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/icomoon.eot");
  src: url("../fonts/icomoon.eot#iefix") format("embedded-opentype"), url("../fonts/icomoon.ttf") format("truetype"), url("../fonts/icomoon.woff") format("woff"), url("../fonts/icomoon.svg#icomoon") format("svg");
  font-family: "icomoon";
  font-display: block;
}
[class^=icon-],
[class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "icomoon" !important;
  speak: never;
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  line-height: 1;
  text-transform: none;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-facebook:before {
  content: "\e900";
  color: #1877f2;
}

.icon-instagram:before {
  content: "\e901";
  color: #e4405f;
}

.icon-latex:before {
  content: "\e902";
  color: #008080;
}

.icon-linkedin:before {
  content: "\e903";
  color: #0077b5;
}

* {
  font-family: "Roboto", sans-serif;
}

.label {
  position: relative;
  z-index: 2;
  margin: 0 auto;
  background-color: #000000;
  padding: 5px 12px;
  width: 112px;
  height: 24px;
  color: #ffffff;
  font-weight: 300;
  font-size: 11px;
  text-align: center;
  text-transform: uppercase;
}
.label_white {
  background-color: #ffffff;
  color: #000000;
}
.label_black {
  background-color: #000000;
  color: #ffffff;
}

.title {
  position: relative;
  z-index: 2;
  margin-top: 38px;
  margin-bottom: 0;
  color: #222222;
  font-weight: 700;
  font-size: 38px;
  line-height: 38px;
  text-align: center;
  text-transform: uppercase;
}
.title_white {
  color: #ffffff;
}
.title_black {
  color: #000000;
}

.subtitle {
  position: relative;
  z-index: 2;
  margin-top: 19px;
  color: #222222;
  font-weight: 400;
  font-size: 17px;
  text-align: center;
}
.subtitle_white {
  color: #ffffff;
}
.subtitle_black {
  color: #000000;
}

header {
  position: relative;
}

nav {
  position: fixed;
  z-index: 5;
  background-color: #212121;
  width: 100%;
  height: 50px;
}

.hamburger {
  display: none;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  width: 26px;
  height: 12px;
}
.hamburger span {
  display: block;
  transition: 0.5s all;
  margin-bottom: 2px;
  background-color: #fff;
  width: 100%;
  height: 2px;
}
.hamburger_active span {
  margin-bottom: -2px;
}
.hamburger_active span:nth-child(1) {
  transform: translateY(3px) rotate(-45deg);
}
.hamburger_active span:nth-child(2) {
  display: none;
}
.hamburger_active span:nth-child(3) {
  transform: translateY(3px) rotate(45deg);
}

.menu {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 13px 70px;
  list-style: none;
}
.menu_link {
  color: #ffffff;
  font-weight: 300;
  font-size: 15px;
}
.menu_item {
  border-right: 2px dotted lime;
  padding-right: 28px;
}
.menu_item:last-child {
  border-right: none;
}

.subheader {
  position: absolute;
  top: 50px;
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 84px;
}
.subheader_logo {
  display: block;
  margin-top: 16px;
  max-width: 100%;
}
.subheader_official {
  opacity: 0.5;
  margin-top: 6px;
  color: #ffffff;
  font-weight: 400;
  font-size: 13px;
}
.subheader_call {
  margin-top: 19px;
  color: #ffffff;
  font-weight: 300;
  font-size: 12px;
  text-align: right;
}
.subheader_phone {
  display: block;
  margin-top: 8px;
  color: #ffffff;
  font-weight: 400;
  font-size: 22px;
  line-height: 12px;
  text-align: right;
  text-decoration: none;
  text-transform: uppercase;
}
.subheader_btn {
  margin-top: 19px;
  box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
  border: none;
  border-radius: 4px;
  background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
  width: 172px;
  height: 46px;
  color: #ffffff;
  font-weight: 400;
  font-size: 14px;
  text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
  text-transform: uppercase;
}
.subheader_btn:hover {
  background-image: none;
  background-color: #1eacc7;
}

.promo {
  background: url("../img/bg/main_bg.jpg") center center/cover no-repeat;
  padding: 150px 0 135px 0;
  min-height: 800px;
}
.promo_header {
  margin: 0;
  color: #ffffff;
  font-weight: 700;
  font-size: 60px;
  line-height: 60px;
  text-align: center;
  text-transform: uppercase;
}
.promo_subheader {
  margin: 0;
  margin-top: 22px;
  color: #ffffff;
  font-weight: 700;
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  text-transform: uppercase;
}
.promo_descr {
  margin-top: 23px;
  color: #ffffff;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
}
.promo_btn {
  display: block;
  margin: 118px auto 0;
  box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
  border: none;
  border-radius: 4px;
  background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
  width: 259px;
  height: 63px;
  color: #ffffff;
  font-weight: 400;
  font-size: 18px;
  text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
  text-transform: uppercase;
}
.promo_btn:hover {
  background-image: none;
  background-color: #1eacc7;
}

.reasons {
  background-color: white;
  padding: 20px 0 64px;
}
.reasons .label {
  background-color: #000000;
  color: #ffffff;
}
.reasons .title {
  color: #222222;
}
.reasons_block {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  padding-top: 16px;
  height: 100%;
}
.reasons_round {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-right: 25px;
  border-radius: 100%;
  background: #1eacc7;
}
.reasons_img_block {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 116px;
  height: 116px;
}
.reasons_descr {
  align-self: flex-start;
  margin-right: 70px;
  width: 100%;
}
.reasons_subtitle {
  color: #1eacc7;
  font-weight: 400;
  font-size: 17px;
  line-height: 21px;
}
.reasons_text {
  color: #202020;
  font-weight: 300;
  font-size: 15px;
  line-height: 21px;
}

.mobile {
  background-color: #fff;
  background: url("../img/bg/mobile_app.png") center center/cover no-repeat;
  padding: 48px 0 79px;
  min-height: 864px;
}
.mobile .label {
  background-color: #000;
  color: #fff;
}
.mobile .title {
  margin-top: 33px;
  color: #000;
}
.mobile .subtitle {
  margin-top: 1px;
}
.mobile .row {
  margin-top: 11px;
}
.mobile_item {
  position: relative;
  margin: 19px auto 0;
  padding-top: 92px;
  width: 320px;
  height: 280px;
}
.mobile_item_1 {
  background: url("../img/bg/mobile_screen_1.jpg") center center/cover no-repeat;
}
.mobile_item_2 {
  background: url("../img/bg/mobile_screen_2.jpg") center center/cover no-repeat;
}
.mobile_item_3 {
  background: url("../img/bg/mobile_screen_3.jpg") center center/cover no-repeat;
}
.mobile_item_4 {
  background: url("../img/bg/mobile_screen_4.jpg") center center/cover no-repeat;
}
.mobile_item_5 {
  background: url("../img/bg/mobile_screen_5.jpg") center center/cover no-repeat;
}
.mobile_item_6 {
  background: url("../img/bg/mobile_screen_6.jpg") center center/cover no-repeat;
}
.mobile_item_subtitle {
  color: #ffffff;
  font-weight: 400;
  font-size: 24px;
  line-height: 31px;
  text-align: center;
  text-transform: uppercase;
}
.mobile_item_plus {
  display: flex;
  position: absolute;
  bottom: 35px;
  left: 50%;
  justify-content: center;
  align-items: center;
  transform: translateX(-50%);
  cursor: pointer;
  margin: 0 auto;
  border-radius: 100%;
  background-color: #000000;
  width: 26px;
  height: 26px;
}
.mobile_item_plus::before {
  display: block;
  transition: 0.3s all;
  background-color: #1eacc7;
  width: 14px;
  height: 2px;
  content: "";
}
.mobile_item_plus::after {
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  transition: 0.3s all;
  background-color: #1eacc7;
  width: 2px;
  height: 14px;
  content: "";
}
.mobile_item_plus:hover::before, .mobile_item_plus:hover::after {
  background-color: #ff5656;
}

.choice {
  position: relative;
  background: url("../img/bg/mobile_app.png") center center/cover no-repeat;
  padding: 57px 0 138px 0;
  min-height: 895px;
}
.choice_img {
  display: block;
  position: relative;
  z-index: 2;
  margin: 56px auto 0 auto;
  max-width: 100%;
}
.choice_descr {
  color: #222222;
  font-weight: 300;
  font-size: 17px;
  line-height: 24px;
  text-align: center;
}
.choice .black {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  background-color: #070716;
  width: 100%;
  height: 499px;
}

.require {
  padding: 68px 0 84px;
}
.require_block {
  margin: 33px 0;
}
.require_block_nmb {
  margin-bottom: 0;
}
.require_title {
  color: #1ca8c3;
  font-weight: 400;
  font-size: 22px;
}
.require_list {
  margin-top: 22px;
  padding-left: 50px;
  list-style-type: none;
}
.require_list li {
  position: relative;
  margin-bottom: 10px;
  font-weight: 300;
  font-size: 17px;
  line-height: 24px;
}
.require_list li::before {
  display: block;
  position: absolute;
  top: 3px;
  left: -32px;
  background: url("../icons/check_blue.png") center center/cover no-repeat;
  width: 21px;
  height: 20px;
  content: "";
}
.require .warning .require_title {
  color: #ff5656;
}
.require .warning .require_list li::before {
  background: url("../icons/check_red.png") center center/cover no-repeat;
}
.world {
  position: relative;
  background: url("../img/bg/map.jpg") center center/cover no-repeat;
  padding: 48px 0 48px;
  min-height: 717px;
}
.world .label {
  width: 75px;
  height: 25px;
}
.world .title {
  margin-top: 26px;
}
.world .subtitle {
  margin-top: 2px;
}
.world_link {
  display: block;
  position: absolute;
  bottom: 48px;
  left: 50%;
  transform: translateX(-50%);
  color: #1fbad6;
  font-weight: 400;
  font-size: 15px;
  line-height: 68px;
  text-align: center;
  text-transform: uppercase;
}
.world_link:hover {
  color: #fff;
  text-decoration: none;
}

footer {
  position: relative;
  padding: 14px 23px 39px;
}
footer a {
  text-decoration: none;
}

.footer_divider {
  margin: 0 auto;
  background: #1fbad6;
  width: 299px;
  height: 4px;
}
.footer_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-top: 32px;
}
.footer_col {
  width: 33%;
}
.footer_social {
  display: flex;
  justify-content: space-between;
  width: 160px;
}
.footer_social_item {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 4px;
  border-radius: 100%;
  background-color: #070716;
  width: 32px;
  height: 32px;
}
.footer_social_item i,
.footer_social_item span::before {
  color: #fff;
}
.footer_links {
  margin: 0 auto;
  width: 390px;
}
.footer_links_main {
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
}
.footer_links_main a {
  color: #070716;
  font-weight: 300;
  font-size: 15px;
  text-transform: uppercase;
}
.footer_links_sub {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}
.footer_links_sub a {
  color: #999999;
  font-weight: 300;
  font-size: 11px;
  text-transform: uppercase;
}
.footer_links_lang {
  display: block;
  margin: 0 auto;
  margin-top: 19px;
  color: #1fbad6;
  font-weight: 400;
  font-size: 11px;
  text-align: center;
  text-transform: uppercase;
}
.footer_mobile {
  display: flex;
  justify-content: flex-end;
}
.footer_mobile a {
  margin: 0 3px;
}

.xs-visible {
  display: none;
}

@media (max-width: 1199px) {
  * {
    --debug-max-width: "lg = 992..1199";
  }
  .menu_item {
    padding-right: 20px;
  }
  .menu_link {
    font-size: 13px;
  }
  .promo_header {
    font-size: 50px;
  }
  .reasons_block {
    align-items: flex-start;
  }
  .mobile_item {
    width: 290px;
  }
  .mobile_item_subtitle {
    font-size: 22px;
  }
  .mobile .choice {
    padding: 55px 0;
    min-height: 840px;
  }
  .mobile .require br {
    display: none;
  }
  .footer_wrapper > div {
    width: 50%;
  }
  .footer_wrapper > div:nth-child(1) {
    width: 25%;
  }
  .footer_wrapper > div:nth-child(3) {
    display: flex;
    justify-content: flex-end;
    width: 25%;
  }
  .footer_mobile {
    display: flex;
  }
  .footer_mobile a {
    margin: 0 3px;
  }
  .footer_mobile img {
    width: 100%;
  }
}
@media (max-width: 991px) {
  * {
    --debug-max-width: "md = 768..991";
  }
  .menu_item {
    padding-right: 8px;
  }
  .menu_link {
    font-size: 10px;
  }
  .subheader_logo img {
    width: 100%;
  }
  .subheader_official {
    font-size: 11px;
  }
  .subheader_phone {
    font-size: 18px;
  }
  .subheader_btn {
    margin-top: 21px;
    width: 100%;
    height: 40px;
  }
  .promo_header {
    font-size: 40px;
  }
  .promo_subheader {
    font-size: 30px;
  }
  .reasons_block {
    flex-direction: column;
    align-items: center;
  }
  .reasons_round {
    margin: 0;
  }
  .reasons_descr {
    margin: 20px 0 0;
  }
  .choice {
    min-height: 800px;
  }
  .choice_img {
    width: 100%;
  }
  .world {
    min-height: 650px;
  }
  .footer_wrapper {
    flex-wrap: wrap;
  }
  .footer_wrapper > div {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    width: 100%;
  }
  .footer_wrapper > div:nth-child(1) {
    width: 100%;
  }
  .footer_wrapper > div:nth-child(3) {
    justify-content: center;
    width: 100%;
  }
}
@media (max-width: 767px) {
  * {
    --debug-max-width: "sm = 576..767";
  }
  .title {
    margin-top: 25px;
  }
  .menu {
    position: fixed;
    top: 0;
    left: -100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: 0.6s all;
    background-color: rgba(0, 0, 0, 0.8);
    padding: 20px 15px;
    width: 50%;
    height: 100vh;
  }
  .menu_active {
    left: 0;
  }
  .menu_item {
    margin-top: 10px;
    border: none;
    padding-right: 0;
  }
  .menu_link {
    font-size: 20px;
  }
  .hamburger {
    display: block;
  }
  .xs-hidden {
    display: none;
  }
  .xs-visible {
    display: block;
  }
  .subheader_phone {
    display: block;
    margin-top: 10px;
    color: #ffffff;
    font-weight: 400;
    font-size: 16px;
    text-align: center;
    text-transform: uppercase;
  }
  .subheader_btn {
    display: block;
    margin: 0 auto;
    margin-top: 10px;
    width: 200px;
    height: 40px;
  }
  .promo_header {
    line-height: 45px;
  }
  .reasons {
    padding: 40px 0;
  }
  .reasons_subtitle {
    text-align: center;
  }
  .reasons_text {
    margin-top: 10px;
  }
  .mobile {
    padding: 40px 0;
  }
  .choice {
    padding: 40px 0;
    min-height: 600px;
  }
  .require {
    padding: 40px 0;
  }
  .world {
    min-height: 450px;
  }
}
@media (max-width: 575px) {
  * {
    --debug-max-width: "xs = 320..575";
  }
  nav {
    height: 30px;
  }
  header {
    height: 30px;
  }
  .title {
    margin-top: 15px;
    font-size: 16px;
    line-height: 16px;
  }
  .subtitle {
    margin-top: 11px;
    font-size: 14px;
    line-height: 21px;
  }
  .subheader {
    top: 30px;
    height: 60px;
  }
  .subheader_official {
    display: none;
  }
  .subheader_logo {
    margin-top: 16px;
  }
  .subheader_phone {
    margin-top: 11px;
    font-weight: 400;
    font-size: 12px;
    text-transform: uppercase;
  }
  .subheader_btn {
    margin-top: 6px;
    width: 110px;
    height: 22px;
