  }
  .menu_link {
    display: block;
    padding: 5px 0;
  }
  .reasons {
    padding: 22px 0 36px 0;
  }
  .reasons_block {
    margin-top: 26px;
  }
  .reasons_round {
    width: 100px;
    height: 100px;
  }
  .reasons_descr {
    margin: 27px 0 0;
    width: 100%;
  }
  .reasons_subtitle {
    font-size: 16px;
    text-align: left;
  }
  .reasons_text {
    margin-top: 0;
  }
  .mobile {
    padding: 25px 0 43px;
  }
  .mobile .row {
    margin-top: 27px;
  }
  .mobile_item {
    padding: 81px 40px 0 40px;
    width: 100%;
    height: 254px;
  }
  .mobile_item_subtitle {
    font-size: 18px;
    line-height: 31px;
  }
  .mobile_item_plus {
    bottom: 28px;
  }
  .choice {
    background: #000;
    padding: 25px 0 33px 0;
    min-height: 390px;
  }
  .choice_img {
    margin-top: 35px;
  }
  .choice_descr {
    margin-top: 23px;
    color: #ffffff;
    font-weight: 300;
    font-size: 17px;
    line-height: 24px;
  }
  .choice .black {
    display: none;
  }
  .hidden-320 {
    display: none;
  }
  .require {
    padding: 25px;
  }
  .require_block {
    margin: 30px 0 0 0;
  }
  .require_title {
    font-size: 16px;
  }
  .require_list {
    margin-top: 5px;
    padding-left: 30px;
  }
  .require_list li {
    margin-bottom: 5px;
    font-size: 14px;
    line-height: 20px;
  }
  .require_descr {
    margin-top: 15px;
    font-size: 14px;
